:root {
  --primary: #d81e5b;
  --secondary: #8c38ff;
  --dark: #131a26;
  --light: #eee;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

body {
  background-color: var(--light);
}

.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

h3 {
  font-size: 3rem;
  font-weight: 400;
  margin: 50px 0px;
  border-bottom: 1px solid black;
}

.interval-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 20px;
}

.interval-container-length {
  display: flex;
}

#break-length, #session-length {
  margin: 10px;
  font-size: 2rem;
  text-align: center;
  border: 2px solid green;
  border-radius: 15px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, .5);
  padding: 10px;
}

#break-label, #session-label, #timer-label {
  text-align: center;
  font-size: 1.5rem;
}

.interval {
  margin: 0 20px;
}

button {
  padding: 10px 20px;
  margin: 0 10px;
  height: 40px;
  cursor: pointer;
  background-color: #263646;
  color: #eee;
  border: none;
}

.time-left {
  border: 2px solid green;
  margin-top: 40px;
  border-radius: 25px;
  width: 250px;
  padding: 20px 0;
  text-align: center;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, .5);
}

h4 {
  margin-bottom: 20px;
}

.timer-minute {
  margin: 10px;
  font-size: 2.5rem;
  text-align: center;
  padding: 10px;
}

.session-buttons {
  margin-top: 20px;
}

#start_stop, #reset {
  text-align: center;
}